p {\n  margin-top: 0;\n  margin-bottom: 0;\n}\n\nimg {\n  width: 100%;\n  height: auto;\n  display: block;\n}\n\nbutton {\n  border: none;\n  color: inherit;\n  font: inherit;\n  text-align: inherit;\n  padding: 0;\n  background-color: transparent;\n}\nul {\n  padding: 0;\n  margin: 0;\n}\n\nul li {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\n.container {\n  width: 141rem;\n  margin: 0 auto;\n}\n\ninput[type='number']::-webkit-inner-spin-button,\ninput[type='number']::-webkit-outer-spin-button {\n  -webkit-appearance: none;\n  margin: 0;\n}\n\ninput[type='number'] {\n  -moz-appearance: textfield;\n}\n\n@media (min-width: 1920px) {\n  html {\n    font-size: 10px;\n  }\n}\n\n@media (max-width: 48em) {\n  html {\n    font-size: 5px;\n    font-size: 1.5625vw;\n    font-size: calc((100 / 375) * 5vw);\n    -webkit-text-size-adjust: none;\n  }\n\n  body {\n    -webkit-text-size-adjust: none;\n  }\n\n  .container {\n    padding: 0 4rem;\n    width: 100%;\n  }\n}\n",".select {\n  position: relative;\n  min-width: 37rem;\n\n  // .select__body\n\n  &__body {\n    position: relative;\n  }\n\n  // .select__title\n\n  &__title {\n    border: 2px solid transparent;\n    border-radius: 0.4rem;\n    width: 100%;\n    background-color: #f1f2f6;\n    cursor: pointer;\n    text-align: left;\n    color: inherit;\n    transition: all 0.3s ease;\n    &:not(._select-opened &, ._select-error &, ._select-disabled &) {\n      @media (any-hover: hover) {\n        &:hover {\n          background-color: #e4e7ee;\n        }\n      }\n    }\n  }\n\n  // .select__value\n\n  &__value {\n    padding: 1.6rem;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    gap: 1rem;\n    height: 5.8rem;\n    width: 100%;\n\n    > * {\n      flex: 1 1 auto;\n    }\n\n    &:after {\n      content: '';\n      flex: 0 0 2.4rem;\n      width: 2.4rem;\n      height: 2.4rem;\n      background-image: url(./assets/images/select-arr.svg);\n      background-size: 2.4rem;\n      background-position: center;\n      background-repeat: no-repeat;\n      transition: transform 0.3s ease;\n    }\n    &._select-label {\n      font-size: 1.8rem;\n      line-height: 144.444%;\n      color: #737b8c;\n      &::before {\n        content: attr(data-sel-label);\n        color: #737b8c;\n        transition: color 0.3s ease;\n      }\n    }\n    &._select-label::before,\n    .select__content {\n      max-width: 31.4rem;\n      overflow: hidden;\n      white-space: nowrap;\n      text-overflow: ellipsis;\n    }\n  }\n\n  // .select__content\n\n  &__content {\n    // hide / show selected value\n    display: none;\n  }\n\n  // .select__text\n\n  &__text {\n    flex: 1 1 auto;\n  }\n\n  // .select__input\n\n  &__input {\n    width: 100%;\n    height: 100%;\n    background-color: transparent;\n  }\n\n  // .select__options\n\n  &__options {\n    position: absolute;\n    z-index: 2;\n    top: calc(100% + 0.8rem);\n    left: 0;\n    padding: 1.2rem 0;\n    min-width: 100%;\n    box-shadow: 0 0.8rem 2.5rem 0 rgba(99, 119, 156, 0.2);\n    border-top: 0;\n    border-top: 0;\n    border-radius: 0.4rem;\n    background-color: #ffffff;\n    color: #1f232b;\n    .select_has-subtitle & {\n      &::before {\n        content: 'Assisteve text';\n        padding-left: 1.6rem;\n        font-family: 'Roboto Flex';\n        font-size: 1.4rem;\n        line-height: 142.857%;\n        color: #737b8c;\n      }\n    }\n  }\n\n  // .select__scroll\n\n  &__scroll {\n    overflow-y: auto;\n    overflow-x: hidden;\n\n    // maximum height\n    max-height: 19rem;\n\n    // scrollbar styles\n    &.simplebar-scrollable-y {\n      .simplebar-track.simplebar-vertical {\n        right: 1.2rem;\n        width: 0.4rem;\n        border-radius: 0.8rem;\n        background-color: #e4e7ee;\n      }\n      .simplebar-scrollbar {\n        min-height: 3.2rem;\n        border-radius: 0.8rem;\n        background-color: #a2adc1;\n      }\n    }\n  }\n  // .select__option\n  &__option {\n    width: 100%